.progress-bar{ 
	height: 40px;  /* Can be anything */
	position: relative;
	background: $white;
	-moz-border-radius: 25px;
	-webkit-border-radius: 25px;
	border-radius: 25px;
    .bar {
      display: block;
      height: 100%;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px;
      background-color: $secondary;
      position: relative;
      overflow: hidden;
    }

    .number {
      font-weight: 700;
      font-size: $s2-text;
      position: absolute;
      left: 20px;
      top: 0;
    }
}

.progress-circle {
    background-color: $tertiary;
    border-radius: 50%;
    display: block;
    height: 13rem;
    margin: 0 auto;
    position: relative;
    width: 13rem;

    &__numbers {
        font-size: $s3-text;
        font-weight: 700;
        text-align: center;
        position: absolute;
        top: 1rem;
        left: 1rem;
        display: flex;
        flex-wrap: wrap;
        height: 11rem;
        width: 11rem;
        justify-content: center;
        align-items: center;  

        .secondary {
            color: $secondary;
        }

        p {
            padding:$baseline 0 0 0;
            font-size:12px;
            line-height: 14px;
        }
    }
  }
  .progress-circle:before {
    align-items: center;
    background-color: #fff;
    border-radius: 50%;
    content: '';
    display: flex;
    font-size: 3rem;
    justify-content: center;
    position: absolute;
    left: 1rem;
    right: 1rem;
    top: 1rem;
    bottom: 1rem;
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  }
  .progress-circle:after {
    background-color: $secondary;
    border-radius: 50%;
    content: '';
    display: inline-block;
    height: 100%;
    width: 100%;
  }
  .progress-circle:hover:before,
  .progress-circle:focus:before {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  /**
  * $step is set to 5 by default, meaning you can only use percentage classes in increments of five (e.g. 25, 30, 45, 50, and so on). This helps to reduce the size of the final CSS file. If you need a number that doesn't end in 0 or 5, you can change the text percentage while rounding the class up/down to the nearest 5.
  */
  .progress-circle[data-progress="0"]:after {
    background-image: linear-gradient(90deg, $tertiary 50%, transparent 50%, transparent), linear-gradient(90deg, $secondary 50%, $tertiary 50%, $tertiary);
  }
  .progress-circle[data-progress="1"]:after {
